* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
  }

  button {
    color: black;
    background: white;
    width: 100px;
    padding: 15px;
    font-size: 1.2rem;
    cursor: pointer;
  }

  hr {
    margin: 10px 0;
  }

  .Detail {
    background: black;
    color: white;
  }

  .logo {
    position: relative;
  }

  .bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background: url('../img/detail_logo2.png');
  }

  .alert {
    color: red;
  }

  .product {
    margin: 20px;
  }

  .product span {
    cursor: pointer;
  }

  .impo img {
    width: 100%;
  }

  .snap_btn {
    display: flex;
    justify-content: space-between;
    margin: 40px 0;
  }

  .snap_btn button {
    width: 45%;
    margin-bottom: 20px;
  }

  .review {
    margin: 20px;
  }

  .review .user_id {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }

  .review .user_id img {
    width: 30px;
  }

  .review .user_idbox {
    display: flex;
    align-items: center;
  }

  .review .user_id div h4 {
    display:inline-flex;
    margin-left: 10px;
  }


  .flex_btw {
    display: flex;
    justify-content: space-between;
  }

  .review_product img {
    width: 50px;
    margin-right: 10px;
  }

  .review_product >.star-rating svg {
    width: 10px;
    height: 10px;
    display: flex;
  }

  .review_product >.star-rating >.star-p {
    font-size: 0.5rem;
  }

  .review_detail h2, p {
    margin: 10px 0;
  }

  .chat {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .chat> input {
    width: 100%;
    height: 40px;
    margin-right: 10px;
  }

  .chat> button {
    background: blue;
    color: white;
    width: 100%;
    height: 40px;
    font-size: 1rem;
    padding: 5px;
  }

  .review_detail button {
    margin: 10px 10px 10px 0;
    width: 100%;
    height: 40px;
    font-size: 1rem;
    padding: 5px;
  }

  .qnabtn button{
    width: 100%;
    height: 40px;
    padding: 5px;
    margin: 50px 0;
  }

  .qnabox {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    cursor: pointer;
  }

  .styled img {
    width: 100%;
  }

  .star-rating {
    display: flex;
    align-items: center;
  }
  
  .star-rating svg {
    cursor: pointer;
    transition: fill 0.2s;
    width: 30px;
    height: 30px;
  }

  .star-p {
    margin-left: 5px;
  }

  

